import { Injectable } from '@angular/core';
import {Http, Response} from '@angular/http';
import {Observable} from 'rxjs/Rx';
import {API_BASE_URL} from './config';
import {Route} from './route';

import 'rxjs/add/operator/map';

@Injectable()
export class RoutesService {
	constructor(private http:Http) { }
  	
  	getRoutes(): Observable<Route[]> {
  		return this.http.get('http://restbus.info/api/agencies/ttc/routes').map(mapRouteResp);
  	}

  	getRoute(id: number): Observable<Route> {
  		return this.http.get('http://restbus.info/api/agencies/ttc/routes/${id}')
  					.map(resp => resp.json().map(toRoute));
  	}

}

function mapRouteResp(resp:Response): Route[] {
	return resp.json().map(toRoute);
}

function toRoute(r: any): Route {
	return <Route>({
		id: r.id,
		title: r.title,
		stops: r.stops
	});
}


